html {
    margin: 0;
    padding: 0;
    width: 100%;
    height: 100%;
}

body {
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 100%;
    margin: 0;
    padding: 0;
}

#root {
    width: 100%;
    height: 100vh;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
}

@-webkit-keyframes circleFull {
    from {
        -webkit-transform: rotate3d(0, 0, 1, -180deg);
                transform: rotate3d(0, 0, 1, -180deg);
    }

    to {
        -webkit-transform: rotate3d(0, 0, 1, 180deg);
                transform: rotate3d(0, 0, 1, 180deg);
    }
}
@keyframes circleFull {
    from {
        -webkit-transform: rotate3d(0, 0, 1, -180deg);
                transform: rotate3d(0, 0, 1, -180deg);
    }

    to {
        -webkit-transform: rotate3d(0, 0, 1, 180deg);
                transform: rotate3d(0, 0, 1, 180deg);
    }
}
@font-face {
    font-family: 'OpenSans';
    src: url(/static/media/OpenSans-Regular.a8874879.ttf);
    font-weight: 500;
}

@font-face {
    font-family: 'OpenSans-Bold';
    src: url(/static/media/OpenSans-Bold.07986db8.ttf);
    font-weight: 500;
}

@font-face {
    font-family: 'OpenSans-Light';
    src: url(/static/media/OpenSans-Light.3c5786e0.ttf);
    font-weight: 500;
}
.loader {
    -webkit-animation: circleFull 2.2s linear infinite;
            animation: circleFull 2.2s linear infinite;
}
.complete-user-box {
    border: 1px solid red;
}

.complete-user-box__text {
    border: 1px solid green;

}

.complete-user-box__button {
    border: 1px solid blue;
}

@media (max-width: 767px) {
    .complete-user-box {}

    .complete-user-box__text {}

    .complete-user-box__button {}
}

@media (min-width: 768px) {
    .complete-user-box {}

    .complete-user-box__text {}

    .complete-user-box__button {}
}

@media (min-width: 1200px) {
    .complete-user-box {
        width: 70%;
    }

    .complete-user-box__text {}

    .complete-user-box__button {}
}
.main-wrapper {
    width: 100%;
    height: 100vh;
    display: flex;
    flex-direction: column;
}
.global-wrapper {
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-start;
  width: 100%;
  height: 100vh;
  box-sizing: border-box;
  overflow: hidden;
}
